
.feature-section{
    padding: 40px 0px;
    overflow: hidden;
}

.feature-section .slick-active {
    border: none !important;
}

.feature-section .slick-slide {
    padding: 10px; /* Add spacing between slides */
}
.feature-section .card {
    border: none !important;
    text-align: center;
    padding: 20px; /* More padding for better spacing */
    margin-bottom: 20px; /* Adjust spacing between cards */
    background-color: #D3D3D3; /* Light gray background */
}

.feature-section .card {
    background-color: #d0cbbd; /* Light gold */
}


.feature-section .card img {
    width: 100%; /* Make the image take the full width of the card */
    height: 250px; /* Adjust height as needed */
    object-fit: cover; /* Ensure images are properly cropped without stretching */
    border-radius: 8px; /* Optional: adds rounded corners */
    margin-bottom: 14px;
}

.feature-section .card-title {
    font-size: 20px; /* Increase the font size */
    font-weight: bold;
}

.feature-section .card-text {
    font-size: 16px;
    color: var(--textclr);
}
.feature-section .card:hover .card-title{
    color:var(--primaryClr)
}

